import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

const routes = [{
        path: "/register",
        component: () => import("../components/Register.vue"),
        hidden: true
    },
    {
        path: "/createbill",
        component: () => import("../components/CreateBill.vue"),
        hidden: true
    },
    {
        path: "/insurance",
        component: () => import("../components/Insurance.vue"),
        hidden: true
    },
    {
        path: "/listmember",
        component: () => import("../components/ListMember.vue"),
        hidden: true
    },
];

export default new Router({
    mode: "history",
    scrollBehavior: () => ({
        y: 0
    }),
    routes
});