</template>
<script>
var moment = require("moment");
export default {
  props: ["value"],
  components: {},
  data: function() {
    return {
      menuCreateBill: true,
      menuRegister: false,
      menuInsurance: false,
      menuPrintBill: false,
      menuButton: true,
      printBillList: true,
      bill: true,
      dataLogin: this.value,
      listBill: [],
      printList: [],
      merid: "",
      mername: "",
      merid: "",
      usernamelogin: "",
      userid: "",
      authenlevel: "",
      time: "",
      output: null
    };
  },

  mounted: function() {
    if (this.$cookie.get("username") != null)
      this.usernamelogin = this.$cookie.get("username");
    this.userid = this.$cookie.get("userid");
    this.mername = this.$cookie.get("mername");
    this.merid = this.$cookie.get("merid");
    this.authenlevel = this.$cookie.get("authenlevel");
  },

  methods: {
    reloadOldBill() {
      // ลบของ POS
      this.$cookie.delete("billNo");
      localStorage.removeItem("datalistPOS");
      localStorage.removeItem("finalDataSave");
      localStorage.removeItem("dataCount");

      // ลบของ QuickLink
      localStorage.removeItem("quickLinkdataCount");
      localStorage.removeItem("quickLinkAddData");
      localStorage.removeItem("quickLinkCountAllinTable");

      // location.reload();
      window.location.href = "/createbill";
    },
    reloadNew() {
      this.$cookie.delete("memberCode");
      this.$cookie.delete("carrierId");
      this.$cookie.delete("billNo");
      localStorage.clear();
      window.location.href = "/createbill";
      // window.location.reload();
    },
    sizeBox() {
      window.open("https://iter.asiazero.com/calbox/", "_blank");
    },
    listMamberSend() {
      window.open("https://mb.945cs.work/login", "_blank");
    },
    listSend() {
      window.open(
        "https://pcr.aplatoo.com/login/?redirect_to=https%3A%2F%2Fpcr.aplatoo.com%2F",
        "_blank"
      );
    },
    typeSize() {
      window.open(
        "https://www.my945.me/warehousezone/adjustsize.php",
        "_blank"
      );
    },
    checkStatus() {
      window.open("https://www.945info.com/", "_blank");
    },
    parcelCapture() {
      window.open("https://my945app.firebaseapp.com/parcel-capture", "_blank");
    },
    findTracking() {
      window.open("https://zip.945.report/", "_blank");
    },
    timeCon(date) {
      var year = moment(date).format("YYYY");
      var year2 = parseInt(year) + 543;
      return moment(date).format(" Do MMMM " + year2 + " " + "H:mm");
    },
    getListBillPrint(bill) {
      console.log("..เลขบิล..", bill);
      window.open(
        "http://206.189.85.185:8100/printPdf/printBillPdf?bill=" + bill,
        "_blank"
      );
    },

    listBillPre() {
      var branchId = this.$cookie.get("merid");
      axios
        .get(
          "http://206.189.85.185:8100/billingPos/listBillngNo?branch_id=" +
            branchId
        )
        .then(resultList => {
          console.log("ListBill", resultList.data);
          this.listBill = resultList.data;

          this.time = parseInt(resultList.data[0].timestamp);

          var contime = moment(this.time);

          console.log("contime", this.time);

          // console.log("timestapm", resultList.data[0].timestamp);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    printBillHistory(bill) {
      axios
        .get("http://206.189.85.185:8100/printBill/printBillAll?bill=" + bill)
        .then(resultStatus => {
          console.log("status", resultStatus.data);
          // if (resultStatus.data == "success") {
          //   $("#confirmModal").modal();
          // } else {
          //   console.log("พิมไม่ผ่าน");
          // }
        })
        .catch(error => {});
    },

    btnRegister() {
      this.merid = this.$cookie.get("merid");
      console.log("merid", this.merid);
      this.menuRegister = true;
      this.menuCreateBill = false;
      this.menuInsurance = false;
      this.$cookie.delete("billNo");
    },
    btnInsurance() {
      this.menuRegister = false;
      this.menuCreateBill = false;
      this.menuInsurance = true;
      this.$cookie.delete("billNo");
    },
    btnList() {
      location.reload();
    },
    logoutParcel() {
      alert("LogOut");
      this.$cookie.delete("username");
      this.$cookie.delete("userid");
      this.$cookie.delete("mername");
      this.$cookie.delete("merid");
      this.$cookie.delete("authenlevel");
      this.$cookie.delete("billNo");
      var path = window.location.href.replace(/(\#.*)/, "");
      window.location.href = path;
      window.location.reload();
    },
    clearPath() {
      var path = window.location.href.replace(/(\#.*)/, "");
      window.location.href = path;
    }
  }
  // computed: {
  //   rowPrint: function() {
  //     return this.printListTest.reduce(function(index, item) {
  //       return  item.lenght % mod % 20;
  //     }, 0);
  //   }
  // },
};
</script>
